@media screen and (min-width: 500px) {
  
  .hero-desc{
    display: block;
    padding-left: 40px;
    margin-top: 5px;
    color: #fff ;
    font-weight: lighter;
  }
  .summary {
      height: 400px;
      max-width: 92%;
      margin: 0 auto;
  }
  .about {
      max-width: 90%;
      height: 400px;
  }

  .benefits {
    max-width: 80%;
    margin: 0 auto;
    margin-top: 50px;
    text-align: left;
}
.summary {
    height: 400px;
    max-width: 92%;
    margin-top: 50px;
}
.about {
    max-width: 90%;
    height: 400px;
}
.benefits-items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 16px;
}
.title {
    font-size: 20px;
    text-align: left;
}
.benefits-text {
    font-size: 12px;
}
.benefits-icon {
    width: 40px;
    height: auto;
}
.benefits-item {
  margin-top: 5px;
    font-size: 12px;
}
.benefits-desc {
  margin-top: 5px;
    font-size: 11px;
    padding-left: 0;
}

#summary-title{
  text-align: center;
}

  
}
 

@media screen and (min-width: 600px) {

      .register-image{
      display: block;
    }
    .signin-image{
      display: block;
    }
  .nav{
    width: 50%;
  }
  

  .culture-list{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 14px;
    grid-row-gap: 16px;
  }

  .home-title {
    font-size: 24px;
  }
  
  .home-ket {
    font-size: 14px;
  }

  .about{
    margin-top: 50px;
  }
  
 
}

@media screen and (min-width: 630px) {
  .about {
      height: 450px;
  }
  .about-desc{
    font-size: 14px;
  }

  .benefits {
    max-width: 85%;
    margin-top: 100px;
}

.hero-bold {
  padding: 0 80px;
  font-size: 28px;
}

.hero-desc {
  color: #fff;
  padding: 5px 80px;
  font-size: 13px;
}

.summary-desc {
  font-size: 14px;
}
.web-name {
  font-size: 32px;
}
}

@media screen and (min-width: 700px) {
  .culture-list{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 14px;
    grid-row-gap: 16px;
  }

  .home-inner{
    top: 75%;
  }

  .home-title {
    font-size: 28px;
  }
  
  .home-ket {
    font-size: 14px;
  }
  .benefits {
    margin-top: 150px;
}

  .detail{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30px;
  }

  .detail-desc p{
    font-size: 14px;
  }
  .detail-desc h4{
    font-size: 16px;
  }

  .detail-info h4{
    font-size: 16px;
  }
  
  .detail-info p{
    font-size: 14px;
  }
  #reviews-list li p{
    font-size: 14px;
  }
  .summary {
    height: 300px;
}
#summary-title {
    text-align: center;
    margin-bottom: 8px;
}
.summary-items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 16px;
}
.summary-desc {
    font-size: 14px;
}

.about {
  height: 250px;
