.weather {
  padding: 25px;
  text-align: center;
  background-color: #3e3e3e7b;
  border-radius: 10px;
}

.weather-title {
  font-size: 20px;
}

.weather-info {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.weather-icon img {
  width: 70px; 
  height: 70px;
  margin-right: 10px;
}

.weather-details p {
  margin: 0;
}

.temperature {
  font-weight: bold;
  font-size: 25px;
  padding-bottom: 5px;
}

.description {
  text-transform: capitalize;
